body {
  margin: 0;
  padding: 0;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 280px;
  width: 100%;
  position: relative;
  min-height: 330px;
  background: #FFF;
  z-index: 0;
}

.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 950px;
  min-height: 145px;
  padding: 0 30px;
}
.content:after {
  content: '';
  display: block;
  background: #EAE9D8;
  border: solid 1px #d3d0c2;
  position: absolute;
  /* top: 100px; */
  left: 0;
  right: 0;
  height: 100%;
  padding: 20px;
  z-index: -1;
}
@media (min-width: 840px) {
  .content:after {
    height: 175px;
  }
}
@media (min-width: 750px) and (max-width: 840px) {
  .content:after {
    height: 215px;
  }
}
@media (min-width: 650px) and (max-width: 750px) {
  .content:after {
    height: 255px;
  }
}

.main {
