          animation: crazy-shake 121ms infinite cubic-bezier(0.215, 0.61, 0.355, 1) !important;
}

#gerar-frase {
  cursor: pointer;
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translateX(0px); transform: translateX(0px) }
  25% { -webkit-transform: translateX(-20px); transform: translateX(-20px); }
  60% { -webkit-transform: translateX(10px); transform: translateX(10px); }
  100% { -webkit-transform: translateX(0px); transform: translateX(0px); }
}

@keyframes shake {
  0% { -webkit-transform: translateX(0px); transform: translateX(0px) }
  25% { -webkit-transform: translateX(-20px); transform: translateX(-20px); }
  60% { -webkit-transform: translateX(10px); transform: translateX(10px); }
  100% { -webkit-transform: translateX(0px); transform: translateX(0px); }
}

@-webkit-keyframes crazy-shake {
  0% { -webkit-transform: translateX(0px) rotate(0); transform: translateX(0px) rotate(0); }
  25% { -webkit-transform: translateX(-30px) rotate(100deg); transform: translateX(-30px) rotate(100deg);}
  60% { -webkit-transform: translateX(20px) rotate(360deg); transform: translateX(20px) rotate(360deg); }
  100% { -webkit-transform: translateX(0px) rotate(0); transform: translateX(0px) rotate(0);}
}

@keyframes crazy-shake {
  0% { -webkit-transform: translateX(0px) rotate(0); transform: translateX(0px) rotate(0); }
  25% { -webkit-transform: translateX(-30px) rotate(100deg); transform: translateX(-30px) rotate(100deg);}
  60% { -webkit-transform: translateX(20px) rotate(360deg); transform: translateX(20px) rotate(360deg); }
  100% { -webkit-transform: translateX(0px) rotate(0); transform: translateX(0px) rotate(0);}
}

